@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #131619;
  margin: 0;
  font-family: 'Plus Jakarta Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn-gradient {
  border-top: 1px solid var(rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.08));
    background: linear-gradient(118deg, rgba(153, 145, 244, 0.17) -47.79%, rgba(135, 117, 248, 0.60) 100%);
}

.active-btn-gradient {
  border-top: 1px solid var(--glass-stroke, rgba(255, 255, 255, 0.08));
  background: linear-gradient(118deg, rgba(215, 237, 237, 0.16) -47.79%, rgba(204, 235, 235, 0.00) 100%);
}

.active-prompt-btn {
  background: linear-gradient(232deg, #967EF8 0%, #4D28E2 191.59%);
}

.apply-btn {
  background: linear-gradient(268deg, #7B5BFF -0.81%, #4B26E1 98.2%);
}

.gradient-dark {
  background: linear-gradient(268deg, #292632 -0.81%, #1F1D25 98.2%);
}

.view-chart-gradient {
  background: linear-gradient(78deg, #191A1B 0.46%, rgba(29, 29, 33, 0.88) 99.67%);
}

.submit-gradient {
  background: linear-gradient(92deg, #3A4CEE 0.76%, #3A4CEE 98.73%);
}

.loader-icon-gradient {
  background: linear-gradient(180deg, #313131 0%, rgba(49, 49, 49, 0.00) 100%);
}

.overlay {
  /* height: 723px; */

  position: absolute;
  bottom: 0;
  border-radius: 16px 16px 0px 0px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  background: linear-gradient(78deg, #191A1B 0.46%, rgba(29, 29, 33, 0.88) 99.67%);
  /* background-color: red; */
}

.loader-screen {
  width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background: rgba(0, 0, 0);
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader-gradient {
  background: var(--glass-modal, rgba(26, 29, 33, 0.96));
}

.hide-scroll::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar {
  width: 6px;
}

.cm {
  height: 100% !important;
}

/* Track */
.cm::-webkit-scrollbar-track {
  background: transparent; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  
    background: #364152;
    opacity: 0.1;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
